  line-height: normal;
  color: #1f1f1f;
  transition: color 0.2s;
}
.new-header__contacts-link:link, .new-header__contacts-link:visited {
  color: #1f1f1f;
}
@media (hover: hover) {
  .new-header__contacts-link:hover {
    color: var(--primary-color);
  }
}

.new-header__callback-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #fff;
  font-size: calc(15px * var(--scale-factor));
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  padding: calc(6px * var(--scale-factor)) calc(10px * var(--scale-factor));
  padding-top: calc(4px * var(--scale-factor));
  background-color: var(--primary-color);
  border-radius: calc(70px * var(--scale-factor));
  white-space: nowrap;
}
.new-header__callback-btn:link, .new-header__callback-btn:visited {
  color: white;
}

.new-header__burger {
  display: none;
}
@media (max-width: 900px) {
  .new-header__burger {
    display: flex;
    flex-direction: column;
    width: calc(30px * var(--scale-factor));
    height: calc(30px * var(--scale-factor));
    gap: calc(4px * var(--scale-factor));
    margin-left: auto;
    align-self: center;
    align-items: center;
    justify-content: center;
    position: relative;
    z-index: 120;
  }
  .new-header__burger > span {
    width: calc(22px * var(--scale-factor));
    background-color: var(--primary-color);
    transition: opacity 0.2s, transform 0.2s;
    height: calc(2px * var(--scale-factor));
  }
  .menu-open .new-header__burger > span:nth-child(1) {
    transform: translateY(calc(6px * var(--scale-factor))) rotate(45deg);
  }
  .menu-open .new-header__burger > span:nth-child(2) {
    opacity: 0;
  }
  .menu-open .new-header__burger > span:nth-child(3) {
    transform: translateY(calc(-6px * var(--scale-factor))) rotate(-45deg);
  }
}